import DayPickerInputForm from '@/components/forms/DayPickerInputForm';
import SelectInputForm from '@/components/forms/SelectInputForm';
import TextInputForm from '@/components/forms/TextInputForm';
import { endpoints } from '@/lib/axios/axios-global';
import { apiGet } from '@/lib/axios/axios-utils';
import { MaintenanceOrder } from '@/types/maintenance';
import { MaintenanceCenter } from '@/types/maintenanceCenter';
import { useEffect, useState } from 'react';
import { useFormContext } from 'react-hook-form';

interface Props {
    maintenanceOrder?: MaintenanceOrder | null;
}
export default function MaintenanceInfo({ maintenanceOrder }: Props) {
    const { setValue } = useFormContext();
    const [maintenanceCenters, setMaintenanceCenters] = useState<{ name: string; value: string }[]>([]);
    const [maintenanceCentersError, setMaintenanceCentersError] = useState<string | null>(null);

    useEffect(() => {
        const fetchMaintenanceCenters = async () => {
            try {
                const response = await apiGet({ endpoint: endpoints.maintenanCenter.select });
                const options = response.data.data.map((center: MaintenanceCenter) => {
                    return { name: center.name, value: String(center.id) };
                });
                setMaintenanceCenters(options);
                setMaintenanceCentersError(null);
            } catch (error) {
                setMaintenanceCentersError('Error fetching maintenanceCenters. Please try again later.');
            }
        };

        fetchMaintenanceCenters();
    }, []);

    useEffect(() => {
        if (maintenanceCenters.length > 0) {
            setValue(
                'maintenance_center_id',
                maintenanceOrder?.maintenance_center?.id ? String(maintenanceOrder?.maintenance_center?.id) : undefined,
            );
        }
    }, [maintenanceCenters]);
    return (
        <div className="mb-6">
            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                <DayPickerInputForm
                    name="maintenance_date"
                    label="تاريخ اخر صيانة"
                    placeholder="ادخل تاريخ اخر صيانة"
                    disabled={(date: Date) => date >= new Date()}
                />
                <DayPickerInputForm
                    name="next_maintenance_date"
                    label="تاريخ الصيانة القادمة"
                    placeholder="ادخل تاريخ الصيانة القادمة"
                />
                <SelectInputForm
                    name="maintenance_center_id"
                    placeholder="أماكن الصيانة المعتمدة"
                    label="مكان الصيانة"
                    options={maintenanceCenters}
                    error={maintenanceCentersError}
                    data={maintenanceOrder}
                />
                <TextInputForm name="maintenance_type" label="نوع العطل" placeholder="ادخل نوع الصيانة " />
                <TextInputForm name="cost" type="number" label="التكلفة" placeholder="ادخل التكلفة" />
                <TextInputForm
                    name="next_maintenance_mileage"
                    type="number"
                    label="عدد كيلومترات الصيانة القادمة"
                    placeholder="ادخل عدد كيلومترات الصيانة القادمة"
                />
            </div>
        </div>
    );
}
