import { z } from 'zod';

export const MaintenanceOrderSchema = z.object({
    maintenance_date: z.date({
        required_error: 'Maintenance last date is required',
    }),
    next_maintenance_date: z.date({
        required_error: 'Next maintenance date last date is required',
    }),
    maintenance_center_id: z
        .string({ required_error: 'Maintenance center is required' })
        .min(1, 'Maintenance center is required'),
    next_maintenance_mileage: z
        .string({ required_error: 'Next maintenance mileage damage is required' })
        .min(1, 'Next maintenance mileage damage is required'),
    maintenance_type: z.string().min(1, 'Maintenance type is required'),
    cost: z
        .string({ required_error: 'Cost maintenance mileage damage is required' })
        .min(1, 'Cost maintenance mileage damage is required'),

    description: z.string().min(1, 'Description is required'),
});

export type MaintenanceOrderFormValues = z.infer<typeof MaintenanceOrderSchema>;
