import { Form } from '@/components/ui/form';
import { useForm } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import { PrimaryButton } from '@/components/buttons/PrimaryButton';
import { apiPost } from '@/lib/axios/axios-utils';
import { endpoints } from '@/lib/axios/axios-global';
import { useMemo, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { SecondaryButton } from '@/components/buttons/SecondaryButton';
import {
    MaintenanceOrderFormValues,
    MaintenanceOrderSchema,
} from '@/features/maintenance/forms/schemas/maintenance-order-schema';
import MaintenanceInfo from './MaintenanceInfo';
import CancelPopup from '@/components/Popups/CancelPopup';
import { useAppSelector } from '@/core/global-store';
import { RichTextEditor } from '@/components/forms/textEditor/RichTextInput';
import { format } from 'date-fns';
import SuccessPopup from '@/components/Popups/SuccessPopup';
import { MaintenanceOrder } from '@/types/maintenance';

interface Props {
    maintenanceOrder?: MaintenanceOrder | null;
}

export default function MaintenanceOrderForm({ maintenanceOrder }: Props) {
    const [error, setError] = useState<string | null>(null);
    const [loading, setLoading] = useState(false);
    const [showSuccessPopup, setShowSuccessPopup] = useState(false);
    const [showCancelPopup, setShowCancelPopup] = useState(false);
    const navigate = useNavigate();

    const state = useAppSelector((state) => state.car.selectedCar);

    const defaultValues = useMemo(
        () => ({
            vehicle_id: maintenanceOrder?.id ? String(maintenanceOrder?.id) : undefined,
            maintenance_date: maintenanceOrder?.maintenance_date
                ? new Date(maintenanceOrder?.maintenance_date)
                : undefined,
            next_maintenance_date: maintenanceOrder?.next_maintenance_date
                ? new Date(maintenanceOrder?.next_maintenance_date)
                : undefined,
            next_maintenance_mileage: maintenanceOrder?.next_maintenance_mileage
                ? String(maintenanceOrder?.next_maintenance_mileage)
                : undefined,
            maintenance_type: maintenanceOrder?.maintenance_type || '',
            cost: maintenanceOrder?.cost ? String(maintenanceOrder?.cost) : undefined,
            description: maintenanceOrder?.description || '',
            maintenance_center_id: maintenanceOrder?.maintenance_center_id
                ? String(maintenanceOrder?.maintenance_center_id)
                : undefined,
        }),
        [maintenanceOrder],
    );

    const form = useForm<MaintenanceOrderFormValues>({
        resolver: zodResolver(MaintenanceOrderSchema),
        defaultValues,
    });

    const handleAddAnother = () => {
        setShowSuccessPopup(false);
    };

    const onSubmit = async (data: MaintenanceOrderFormValues) => {
        setError(null); // Clear previous errors
        if (state || maintenanceOrder) {
            setLoading(true);
            const formatedData = {
                ...data,
                next_maintenance_date: format(data.next_maintenance_date, 'yyyy-MM-dd'),
                maintenance_date: format(data.maintenance_date, 'yyyy-MM-dd'),
                vehicle_id: maintenanceOrder ? maintenanceOrder.vehicle.id : state?.id,
                status: 'completed',
            };
            try {
                await apiPost({
                    endpoint: maintenanceOrder
                        ? endpoints.maintenance.update + maintenanceOrder.id
                        : endpoints.maintenance.create,
                    body: formatedData,
                    config: { headers: { 'Content-Type': 'multipart/form-data' } },
                });
                setShowSuccessPopup(true);
            } catch (error: any) {
                if (error.response?.data?.errors) {
                    // Handle field-specific errors
                    Object.entries(error.response.data.errors).forEach(([field, messages]) => {
                        form.setError(field as any, {
                            type: 'manual',
                            message: (messages as string[]).join(', '),
                        });
                    });
                } else {
                    // Handle generic error
                    let errorMessage = 'حدث خطأ أثناء إرسال البيانات. يرجى المحاولة مرة أخرى.';
                    if (error.response) {
                        errorMessage = error.response.data.msg || errorMessage;
                    } else if (error.request) {
                        errorMessage = 'تعذر الاتصال بالخادم. يرجى التحقق من اتصالك بالإنترنت.';
                    }
                    setError(errorMessage);
                }
            } finally {
                setLoading(false);
            }
        } else {
            setError('قم بإختيار سيارة للمتابعة');
        }
    };

    return (
        <>
            <Form {...form}>
                <form onSubmit={form.handleSubmit(onSubmit)} className="mb-8">
                    {error && (
                        <div className="mb-4 p-4 bg-red-100 border border-red-400 text-red-700 rounded-lg">
                            <div className="flex justify-between items-center">
                                <span className="">{error}</span>
                                <button onClick={() => setError(null)} className="text-red-700 hover:text-red-900">
                                    ×
                                </button>
                            </div>
                        </div>
                    )}
                    <MaintenanceInfo maintenanceOrder={maintenanceOrder} />
                    <RichTextEditor
                        form={form}
                        data={maintenanceOrder?.description}
                        name="description"
                        placeholder="ادخل ملاحظات عن العميل"
                    />
                    {/* <div className="prose max-w-none" dangerouslySetInnerHTML={{ __html: form.watch('content') }} /> */}

                    <div className="flex flex-row justify-end items-center mt-8">
                        {maintenanceOrder ? (
                            <SecondaryButton
                                type="button"
                                className="xl:px-[101px] xl:py-[18px] text-[#A21515] border border-red-error-50 xl:font-bold bg-red-error-50"
                                onClick={() => setShowCancelPopup(true)}
                            >
                                إلغاء الطلب
                            </SecondaryButton>
                        ) : (
                            <SecondaryButton
                                type="button"
                                className="xl:px-[125px] xl:py-[18px] border bordr-[#E8EFF6] xl:font-bold"
                                onClick={() => setShowCancelPopup(true)}
                            >
                                الغاء
                            </SecondaryButton>
                        )}
                        <div>
                            {maintenanceOrder ? (
                                <PrimaryButton
                                    className="xl:px-[124px] xl:py-[18px] xl:font-bold ms-6"
                                    disabled={loading}
                                >
                                    {loading ? 'جاري التحميل...' : 'حفظ التغييرات'}
                                </PrimaryButton>
                            ) : (
                                <PrimaryButton
                                    className="xl:px-[154px] xl:py-[18px] xl:font-bold ms-6"
                                    disabled={loading}
                                >
                                    {loading ? 'جاري التحميل...' : 'اضافة'}
                                </PrimaryButton>
                            )}
                        </div>
                    </div>
                </form>
            </Form>

            <CancelPopup
                open={showCancelPopup}
                onClickButton1={() => {
                    setShowCancelPopup(false);
                }}
                onClickButton2={() => {
                    navigate('/maintenance/list');
                    setShowCancelPopup(false);
                }}
                text1={maintenanceOrder ? 'إلغاء تعديل بيانات طلب الصيانة' : 'إلغاء طلب الصيانة'}
                text2={
                    maintenanceOrder
                        ? 'أنت على وشك إلغاء عملية تعديل بيانات طلب صيانة دون إكمال البيانات المطلوبة. سيتم تجاهل جميع البيانات المدخلة، هل أنت متأكد؟'
                        : 'أنت على وشك إلغاء عملية إضافة طلب صيانة دون إكمال البيانات المطلوبة. سيتم تجاهل جميع البيانات المدخلة، هل أنت متأكد؟'
                }
                textButton1="إكمال الطلب"
                textButton2="إلغاء الطلب"
            />

            <SuccessPopup
                text1={maintenanceOrder ? 'تم تعديل طلب الصيانة بنحاح' : 'تم إضافة طلب الصيانة بنحاح'}
                text2={
                    maintenanceOrder
                        ? 'تم تعديل بيانات طلب الصيانة المطلوبة، يمكنك المتابعه مع مركز الصيانة.'
                        : 'تم تسجيل بيانات طلب الصيانة المطلوبة، يمكنك المتابعه مع مركز الصيانة.'
                }
                textButton1="العودة لقائمة الصيانة"
                textButton2={maintenanceOrder ? 'تعديل بيانات اخرى' : 'إضافة طلب أخر'}
                open={showSuccessPopup}
                onAddAnother={handleAddAnother}
                onReturnToList={() => {
                    navigate('/maintenance/list');
                    setShowSuccessPopup(false);
                }}
            />
        </>
    );
}
